.trackContainer{
    display: flex;
    justify-content: space-between;
    padding-bottom: 10px;
    padding-top: 10px;
    border-bottom: 1px solid gray;
    margin-left: 10px;
    height: 20%;
    max-height: 20%;
}

.title{
    font-family: 'Work Sans', sans-serif;
    font-size: 20px;
    font-weight: 700;
    /*line-height: 30px;*/
    padding-bottom: 5px;
}

.artist, .album{
    font-family: 'Work Sans', sans-serif;
}

.album{
    font-style: italic;
}

.button{
    height: 30px;
    width: 30px;
    text-align: center;
    cursor: pointer;
    background: black;
    color: white;
    border: 1px solid white;
    font-weight: 700;
    font-family: 'Work Sans', sans-serif;
    border-radius: 5px;
    
}

.right{
    align-self: center;
}

.left{
    display: flex;
    gap: 5px;
}

.leftContent{
    width: 75%;
    
}

.image{
    width: 70px;
    height: 70px;
}

.imageContainer{
    align-self: center;
}

@media only screen and (max-width: 480px) {
    .image{
        width: 40px;
        height: 40px;
    }

    .title{
        font-size: 14px;
    }

    .button{
        height: 25px;
        width: 25px;
    }

    .artist, .album{
        font-size: 12px;
    }

    .leftContent{
        align-self: center;
    }
}